<template>
  <input type="checkbox" />
</template>
<script>
import { setTimeout } from "timers";
export default {
  props: {
    value: {
      default: "on"
    },
    checkboxClass: {
      type: String,
      default: "icheckbox_square-blue"
    },
    radioClass: {
      type: String,
      default: "iradio_square-blue"
    },
    checked: {
      type: Boolean,
      default: false
    },
    trueValue: {
      default: 1
    },
    falseValue: {
      default: 0
    }
  },
  watch: {
    value(val) {
      if (val === this.trueValue) {
        $(this.$el).iCheck("check");
      } else {
        $(this.$el).iCheck("uncheck");
      }
    },
    checked(val) {
      if (val) {
        $(this.$el).iCheck("check");
      } else {
        $(this.$el).iCheck("uncheck");
      }
    }
  },
  created() {},
  mounted() {
    $(this.$el).iCheck({
      checkboxClass: this.checkboxClass,
      radioClass: this.radioClass
    });

    this.$el.value = this.value;

    if (this.checked) {
      $(this.$el).iCheck("check");
    }

    if (this.value == this.trueValue) {
      $(this.$el).iCheck("check");
    }

    $(this.$el).on("ifClicked", event => {
      this.$nextTick(() => {
        if (this.$el.checked) {
          this.$emit("input", this.trueValue);
        } else {
          this.$emit("input", this.falseValue);
        }
      });
    });
  }
};
</script>

